

.nav {
    background: #1d7baa;
    border-radius: 10px;
    margin-left:100px;
    margin-right: 100px;
    height: 50px;
    display:flex;
    align-items: center;
}
#home {
    margin-left: 1%;
    color: white;
    font-family: 'Courier New', Courier, monospace;
    font-size: 26px;
}
#addNoteContainer{
    margin:2%;
    /* background: red; */
}
#addNoteForm {
    /* background: #b3d5e6; */
    margin:0 auto;
    width:50%;
    text-align: center;
}
.addNoteInput {
    border:none;
    width:98%;
    height: 20px;
    border-radius: 10px;
    padding: 2%;
    margin:1%;
    font-size: 20px;
}
#addNoteButton {
    background: #e4f5fd;
    border-radius: 10px;
    border: none;
    padding: 2%;
    width: 10%;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif
}
#allNotesContainer{
    display: grid;
    grid-template-rows: 25% 25% 25% 25%;
    grid-template-columns: 25% 25% 25% 25%;
}
#noteBox{
    background: #aaccdd;
    margin: 5%;
    border-radius: 10px;
    padding: 3%;
}
#noteTitle{
    font-size: 22px;
    font-family: Arial, Helvetica, sans-serif;
    /* background: #cccccc; */
    
    /* the following two are 
    needed to center the title*/
    display: block;
    text-align: center;
}
#noteText{
    display:block;
}

